@font-face {
  font-family: "PressStart2P";
  src: url("./fonts/PressStart2P-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "PixelMplus12";
  src: local("GoldmanBold"), url("./fonts/PixelMplus12-Regular.ttf") format("truetype");
}

html,
body {
  background-color: #00008b;
  color: white;
  font-family: "PressStart2P";
}

p {
  margin: 0;
}

.group-text-connect {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.info-container {
  background-image: url("./icon//black_bg.png");
  padding: 0px 20px 20px 20px;
  border-top: 2px solid #a8ffff;
  border-bottom: 2px solid #a8ffff;
}

.group-container-info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.land-divide {
  width: 100%;
  margin-top: 30px;
}

.detail-info {
  margin: 40px 0 0 10px;
}

.info-text {
  font-size: 13px;
}

.price-info {
  font-size: 9px;
  margin: 5px 0px 15px 0px;
}

.header-content-support {
  font-size: 20px;
  text-align: center;
  padding: 0 10px 0 10px;
}

.button-switch {
  margin-top: 15px;
  font-size: 15px;
  border: 2px solid white;
  color: white;
  background-color: #0f3460;
  padding-top: 0px;
  height: 60px;
}

.custom-button {
  border-radius: 5px;
}

.container-app {
  text-align: center;
}

.header-button {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px 0;
  height: 50px;
}

.group-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.group-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-bottom: 1px solid #fff;
}

.group-content {
  margin-top: 40px;
  position: relative;
  z-index: 0;
}

.group-first-content {
  padding-top: 40px;
  border-top: 1px solid #fff;
}

.heading-title {
  padding: 20px 0 20px 0;
  margin-top: 20px;
  font-size: 20px;
}

.btn-connect {
  color: white;
  font-size: 10px;
  height: 35px;
}

.btn-grad {
  border: 2px solid white;
  color: white;
  width: 100px;
  height: 35px;
  font-size: 9px;
}

.english-content {
  padding-bottom: 20px;
  margin: 0 20px 0 20px;
  text-align: center;
  font-size: 19px;
}

.japan-content {
  font-family: "PixelMplus12";
  margin: 0 20px 0 20px;
  text-align: center;
  font-weight: 600;
  font-size: 13px;
}

.group-footer-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

.group-footer-icon a {
  background-color: #fff;
  display: grid;
  place-items: center;
  padding: 6px;
  border-radius: 50%;
}

.footer-content {
  font-size: 10px;
  padding-top: 10px;
  padding-bottom: 20px;
}

.heading-content {
  font-size: 14px;
  padding-bottom: 10px;
}

.team-role {
  padding-top: 5px;
  font-size: 9px;
  color: #a8ffff;
}

.english-content {
  padding-bottom: 20px;
  text-align: center;
  font-size: 11px;
}

.japan-content {
  font-family: "PixelMplus12";
  text-align: center;
  font-weight: 600;
}

.group-image {
  display: flex;
  padding: 10px 0 20px 0;
  justify-content: space-between;
}

.btn-custom {
  margin: 3px 0 3px 0;
  padding: 0;
  background-color: #fff;
}

.address-text {
  font-size: 10px;
  margin: 10px 10px 0 0;
}

.group-footer {
  margin-top: 30px;
}

.team-name {
  font-size: 9px;
}

.background-image {
  position: absolute;
  inset: 0;
  z-index: -1;
  background: url("./icon//bg.svg") no-repeat;
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .group-image {
    margin: 0 150px 0 150px;
  }

  .content-space {
    margin: 0 100px 0 100px;
  }

  .team-image {
    width: 130px;
    height: 130px;
  }

  .background-image {
    top: 16%;
    transform: scaleY(0.6);
  }

  .image_full {
    display: block;
  }

  .image_mobile {
    display: none;
  }
}

@media only screen and (min-width: 1024px) {
  .group-image {
    margin: 0 250px 0 250px;
  }

  .content-space {
    margin: 0 160px 0 160px;
  }

  .team-image {
    width: 130px;
    height: 130px;
  }

  .background-image {
    top: 16%;
    transform: scaleY(0.6);
  }

  .image_full {
    display: block;
  }

  .image_mobile {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .group-image {
    flex-direction: column;
    gap: 30px;
  }

  .content-space {
    margin: 0 30px 0 30px;
  }

  .team-image {
    width: 130px;
    height: 130px;
  }

  .address-text {
    width: 40%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-right: 10px;
  }

  .english-content {
    font-size: 10px;
  }

  .heading-title {
    margin-top: 20px;
    font-size: 16px;
  }

  .info-text {
    font-size: 13px;
  }

  .team-name {
    font-size: 8px;
  }

  .team-role {
    padding-top: 5px;
    font-size: 8px;
  }

  .group-header {
    padding-bottom: 20px;
  }

  .background-image {
    display: none;
  }

  .background-image-team {
    background: url("./icon//bg.svg") no-repeat;
    background-size: 300% 125px;
  }

  .image_full {
    display: none;
  }

  .image_mobile {
    display: block;
  }
}

@media only screen and (min-width: 440px) and (max-width: 767px) {
  .background-image-team {
    background: url("./icon//bg.svg") no-repeat;
    background-size: 200% 125px;
  }
}
